<template>
    <div class="uk-animation-slide-bottom-medium background">  
        <div class="uk-width-2-3 uk-align-center uk-align-center uk-margin-remove-top">

            <div class="uk-margin">
                <ul uk-accordion>
                    <li class="uk-open">
                        <a class="uk-accordion-title">Data</a>
                        <div class="uk-accordion-content">
                            <div class="uk-grid-small" uk-grid>
                                <div class="uk-width-expand">
                                    <textarea v-model="input" class="uk-textarea" rows="5" placeholder="input"></textarea>
                                </div>
                                
                                <div class="uk-width-auto">
                                    <div>
                                        <button @click = "GetModelFromInput()" class="uk-button uk-button-default">Import</button>
                                    </div>
                                    <div>
                                        <button @click = "GetOutputFromModel()" class="uk-button uk-button-default">Export</button>
                                    </div>
                                </div>
                                
                                <div class="uk-width-expand">
                                    <textarea v-model="output" class="uk-textarea" rows="5" placeholder="output"></textarea>
                                </div>
                            </div>	
                        </div>
                    </li>
                </ul>
            </div>
           
            <div v-if="model != null" class="uk-margin">
                <div class="uk-card uk-card-default uk-card-body">
                    <ul uk-accordion>
                        <li v-for="(item, index) in model.portfolioData" :key="'title' + index">
                            <a class="uk-accordion-title" href="#">
                                {{ item.title }}
                            </a>
                            <div class="uk-accordion-content">
                                <div class="uk-margin-small">
                                    <button @click="RemoveItem(index)" class="uk-button uk-button-danger uk-button-small">Delete</button>
                                </div>
                                    
                                <div class="uk-grid uk-grid-small" uk-grid>
                                    <div class="uk-width-1-2">
                                        <!-- DATA -->
                                        <input v-model="item.name" class="uk-input uk-form-small" type="text" placeholder="Name" uk-tooltip="Name">
                                        <input v-model="item.title" class="uk-input uk-form-small" type="text" placeholder="Title" uk-tooltip="Title">
                                        <input v-model="item.tools" class="uk-input uk-form-small" type="text" placeholder="Tools" uk-tooltip="Tools">
                                        <input v-model="item.category" class="uk-input uk-form-small" type="text" placeholder="Category" uk-tooltip="Category">
                                        <input v-model="item.team" class="uk-input uk-form-small" type="text" placeholder="Team" uk-tooltip="Team">
                                        <input v-model="item.role" class="uk-input uk-form-small" type="text" placeholder="Role" uk-tooltip="Role">
                                        <label><input v-model="item.finished" class="uk-checkbox" type="checkbox" uk-tooltip="Finished">&nbsp;</label>
                                        <input v-model="item.duration" class="uk-input uk-form-small" type="text" placeholder="Duration" uk-tooltip="Duration">
                                        <textarea v-model="item.shortDescription" class="uk-textarea uk-form-small" rows="2" placeholder="Short description" uk-tooltip="Short description"></textarea>
                                        <textarea v-model="item.description" class="uk-textarea uk-form-small" rows="8" placeholder="Description" uk-tooltip="Description"></textarea>
                                        <input v-model="item.logo" class="uk-input uk-form-small" type="text" placeholder="Logo" uk-tooltip="Logo">
                                        <input v-model="item.tags" class="uk-input uk-form-small" type="text" placeholder="Tags" uk-tooltip="Tags">
                                        
                                        <!-- LINKS -->
                                        <div v-for="(link, index) in item.links" :key="'link' + index">
                                            <div class="uk-grid uk-grid-collapse" uk-grid>
                                                <div class="uk-width-auto">
                                                    <label><input v-model="link.enabled" class="uk-checkbox" type="checkbox" uk-tooltip="Enabled">&nbsp;</label>
                                                </div>
                                                <div class="uk-width-expand">
                                                    <input v-model="link.name" class="uk-input uk-form-small" type="text" placeholder="Name" uk-tooltip="Name">
                                                </div>
                                                <div class="uk-width-expand">
                                                    <input v-model="link.icon" class="uk-input uk-form-small" type="text" placeholder="Icon" uk-tooltip="Icon">
                                                </div>
                                                <div class="uk-width-expand">
                                                    <input v-model="link.url" class="uk-input uk-form-small" type="text" placeholder="Url" uk-tooltip="Url">
                                                </div>
                                                <div v-if="index != 0">
                                                    <button @click="MoveContentLink(item, index-1, index)" class="uk-button uk-button-default uk-button-small uk-height-1-1">
                                                        <span :uk-icon="'triangle-up'"></span>
                                                    </button>
                                                </div>
                                                <div v-if="index != item.links.length -1">
                                                    <button @click="MoveContentLink(item, index+1, index)" class="uk-button uk-button-default uk-button-small uk-height-1-1">
                                                        <span :uk-icon="'icon: triangle-down'"></span>
                                                    </button>
                                                </div>
                                                <div>
                                                    <button @click="RemoveContentLink(item, index)" class="uk-button uk-button-default uk-button-small uk-height-1-1">
                                                        <span :uk-icon="'close'"></span>
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="uk-margin-small">
                                            <button @click="AddContentLink(item)" class="uk-button uk-button-default uk-button-small">Add link</button>
                                        </div>
                                        
                                        <!-- ACHIEVEMENTS -->
                                        <!-- <input v-model="item.achievements" class="uk-input uk-form-small" type="text" placeholder="Achievements" uk-tooltip="Achievements"> -->
                                        
                                        <!-- CONTENT -->
                                        <div v-for="(content, index) in item.content" :key="'content' + index">
                                            <div class="uk-grid uk-grid-collapse" uk-grid>
                                                <div class="uk-width-1-6">
                                                    <select v-model="content.type" class="uk-select uk-form-small">
                                                        <option value="youtube">Youtube</option>
                                                        <option value="image">Image</option>
                                                        <option value="model">Model</option>
                                                    </select>
                                                </div>
                                                <div class="uk-width-expand">
                                                    <input v-model="content.name" class="uk-input uk-form-small" type="text" placeholder="File name" uk-tooltip="File name">
                                                </div>
                                                <div v-if="content.type == 'youtube' || content.type == 'model'" class="uk-width-expand">
                                                    <input v-model="content.src" class="uk-input uk-form-small" type="text" placeholder="Source" uk-tooltip="Source">
                                                </div>
                                                <div v-if="index != 0">
                                                    <button @click="MoveContentItem(item, index-1, index)" class="uk-button uk-button-default uk-button-small uk-height-1-1">
                                                        <span :uk-icon="'triangle-up'"></span>
                                                    </button>
                                                </div>
                                                <div v-if="index != item.content.length -1">
                                                    <button @click="MoveContentItem(item, index+1, index)" class="uk-button uk-button-default uk-button-small uk-height-1-1">
                                                        <span :uk-icon="'icon: triangle-down'"></span>
                                                    </button>
                                                </div>
                                                <div>
                                                    <button @click="RemoveContentItem(item, index)" class="uk-button uk-button-default uk-button-small uk-height-1-1">
                                                        <span :uk-icon="'close'"></span>
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="uk-margin-small">
                                            <button @click="AddContentItem(item)" class="uk-button uk-button-default uk-button-small">Add content</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                    <div>
                        <button @click="AddItem()" class="uk-button uk-button-default uk-button-small">Add</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data (){
        return {
			input: '',
			output: '',
            model: null,
        }
    },

    methods: {
        AddContentItem(item){
            item.content.push({ type:'', name:'', src:'' });
        },
			
        RemoveContentItem(item, index){
            item.content.splice(index, 1);
        },
        
        MoveContentItem(item, to, from){
            let element = item.content[from];
            item.content.splice(from, 1);
            item.content.splice(to, 0, element);
        },
        
        AddContentLink(item){
            item.links.push({ enabled: 0, name:'', icon:'', url:'' });
        },
        
        RemoveContentLink(item, index){
            item.links.splice(index, 1);
        },
        
        MoveContentLink(item, to, from){
            let element = item.links[from];
            item.links.splice(from, 1);
            item.links.splice(to, 0, element);
        },
        
        AddItem(){
            this.model.portfolioData.push({
                name: '',
                title: 'New item',
                tools: '',
                category: '',
                team: '',
                role: '',
                finished: 0,
                duration: '',
                year: '',
                shortDescription: '',
                description: '',
                logo: '',
                sideImg: '',
                tags: [],
                links: [],
                achievements: { 
                    awards: [],
                    nominations: [] 
                },
                content: []
            });
        },
        
        RemoveItem(index){
            this.model.portfolioData.splice(index, 1);
        },
    
        GetModelFromInput(date){
            this.model = JSON.parse(this.input);
            
            this.model.portfolioData.forEach(function(item) {
                item.description = item.description.split("<br>").join("\n");
                item.description = item.description.split("</br>").join("\n");
            });
        },
        
        GetOutputFromModel(date){
            this.model.portfolioData.forEach(function(item) {
                item.description = item.description.split("\n").join("<br>");
            });
        
            this.output = JSON.stringify(this.model);
        }
    }
}
</script>